body{
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(92, 206, 159);
}
.d{
  font-size: 30px;
  color: rgb(92, 206, 159);
  margin: 0px 0px;
  padding-top: 10px;
  padding-right: 10px;
  height: 10vh;
}

.btns{
  margin: 0px 0px;
  width: 400px;
  height: 55vh;
}
.row{
  margin: 0px 0px;
  width: 400px;
  height: 11vh;
}
.con{
  display: flex;
  flex-direction: column;
  width: 400px;
  align-items: end;
  height: 65vh;
  background-color: black;
}
button{
  border-color: rgb(0, 0, 0);
  color: rgb(0, 0, 0);
  background-color: rgb(191, 209, 226);
}
#clear{
  background-color: rgb(172, 2, 2);
}
#equals{
  background-color: rgb(95, 236, 193);
}
#lg{
  background-color: rgb(50, 225, 211);
}
#divide,#multiply,#subtract,#add{
  background-color: rgb(170, 228, 115);
}
